@Component({
  selector: 'app-dashboard-v1',
  templateUrl: './v1.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DashboardV1Component implements OnInit {
  todoData: any[] = [
    {
      completed: true,
      avatar: '1',
      name: '苏先生',
      content: `请告诉我，我应该说点什么好？`,
    },
    {
      completed: false,
      avatar: '2',
      name: 'はなさき',
      content: `ハルカソラトキヘダツヒカリ`,
    },
    {
      completed: false,
      avatar: '3',
      name: 'cipchk',
      content: `this world was never meant for one as beautiful as you.`,
    },
    {
      completed: false,
      avatar: '4',
      name: 'Kent',
      content: `my heart is beating with hers`,
    },
    {
      completed: false,
      avatar: '5',
      name: 'Are you',
      content: `They always said that I love beautiful girl than my friends`,
    },
    {
      completed: false,
      avatar: '6',
      name: 'Forever',
      content: `Walking through green fields ，sunshine in my eyes.`,
    },
  ];

  webSite: any[];
  salesData: any[];
  offlineChartData: any[];


  q: any = {
    pi: 1,
    ps: 10,
    sorter: '',
    status: null,
    statusList: [],
  };
  data: any[] = [];
  loading = false;
  status = [
    { index: 0, text: '大融城', value: false, type: 'default', checked: false },
    {
      index: 1,
      text: '时代',
      value: false,
      type: 'processing',
      checked: false,
    },
    { index: 2, text: '江津', value: false, type: 'success', checked: false },
    { index: 3, text: '永川', value: false, type: 'error', checked: false },
  ];
  @ViewChild('st', { static: true })
  st: STComponent;
  columns: STColumn[] = [
    { title: '', index: 'key', type: 'checkbox' },
    { title: '店铺', index: '店铺' },
    { title: '是否VIP', index: '是否VIP', format: data => `${data[`是否VIP`] || 'NO'}` },
    { title: '等级', index: '等级' },
    { title: '姓名', index: '姓名' },
    { title: 'VIP卡号', index: 'VIP卡号', format: data => `${data[`VIP卡号`] || 0}` },
    { title: 'QQ邮箱', index: 'QQ邮箱' },
    { title: '生日', index: '生日' },
    { title: '电话', index: '电话' },
    { title: '累计数量', index: '累计数量' },
    { title: '上年结余', index: '上年结余', format: data => `${data[`上年结余`] || 0}` },
    { title: '本年积分', index: '本年积分' },
    { title: '兑换', index: '兑换', format: data => `${data[`兑换`] || 0}` },
    { title: '剩余', index: '剩余' },

    {
      title: '操作',
      buttons: [
        {
          text: '配置',
          click: (item: any) => this.msg.success(`配置${item.no}`),
        },
        {
          text: '删除',
          click: (item: any) => this.msg.success(`删除${item.no}`),
        },
      ],
    },
  ];
  selectedRows: STData[] = [];
  description = '';
  totalCallNo = 0;
  expandForm = false;

  constructor(
    private http: HttpClient,
    private cdr: ChangeDetectorRef,
    public msg: NzMessageService,
    private modalSrv: NzModalService
  ) { }

  ngOnInit() {
    this.http.get('/chart').subscribe((res: any) => {
      this.webSite = res.visitData.slice(0, 10);
      this.salesData = res.salesData;
      this.offlineChartData = res.offlineChartData;
      this.cdr.detectChanges();
    });
    this.getData()
  }

  onloadXlsx(): void {
    // XLSX.read()
  }

  /** 
   * 加载文件
   */
  load(): Promise<any> {
    return new Promise(resolve => {
      this.http.get('assets/ivviwork/ivvi.csv', { responseType: `text` }).subscribe(data => {
        const json = csvToJson.csvToJson(data);
        resolve(json)
      })
    });
  }

  async getData() {
    this.loading = true;
    this.q.statusList = this.status.filter(w => w.checked).map(item => item.index);
    if (this.q.status !== null && this.q.status > -1) {
      this.q.statusList.push(this.q.status);
    }
    this.loading = true;
    this.data = await this.load();
    console.log(this.q);
    if (this.q['电话'] || this.q['店铺'] || this.q['姓名']) {
      this.data = this.data.filter(cur =>
        (this.q['电话'] == cur['电话']))
    }

    this.cdr.detectChanges();
    this.loading = false;
  }

  stChange(e: STChange) {
    console.log(e);

    switch (e.type) {
      case 'checkbox':
        this.selectedRows = e.checkbox!;
        this.totalCallNo = this.selectedRows.reduce((total, cv) => total + cv.callNo, 0);
        this.cdr.detectChanges();
        break;
      case 'filter':
        this.getData();
        break;
    }
  }

  remove() {
    // this.http.delete('/rule', { nos: this.selectedRows.map(i => i.no).join(',') }).subscribe(() => {
    //   this.getData();
    //   this.st.clearCheck();
    // });
  }

  approval() {
    this.msg.success(`审批了 ${this.selectedRows.length} 笔`);
  }

  add(tpl: TemplateRef<{}>) {
    this.modalSrv.create({
      nzTitle: '新建规则',
      nzContent: tpl,
      nzOnOk: () => {
        this.loading = true;
        this.http.post('/rule', { description: this.description }).subscribe(() => this.getData());
      },
    });
  }

  reset() {
    // wait form reset updated finished
    setTimeout(() => this.getData());
  }
}
